.Signup {
    background-color: white;
    width: 100%;
    border: 1px solid #eee;
    box-sizing: border-box;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px; 
    text-align: center;
}
.Signup input {
    display: block;
    font-size: 18px;
    padding: 5px;
    margin: 10px auto;
}